import { twMerge } from "tailwind-merge";

const Button = ({ className, fit = true, children, ...rest }) => {
    return (
        <button className={twMerge("shadow-md bg-primary w-fit px-12 rounded-[8px] text-foreground py-4 font-[500]", !fit && "w-full", className)} {...rest}>
            {children}
        </button>
    )
}

export default Button