import React, { useState } from 'react'
import TitleCard from '../../../ui/titleCard'
import MaxWidthContainer from '../../../ui/maxWidthContainer';
import Button from '../../../ui/button';
import { twMerge } from 'tailwind-merge';
import { useTranslation } from 'react-i18next';

import { Global, Mobile, ArrowCircleRight, EmojiHappy, People, Award } from 'iconsax-react';
import AnimatedDiv from '../../../ui/animatedDiv';

const Services = () => {
  const [selected, setSelected] = useState('web');

  const { t, i18n } = useTranslation();

  const lang = i18n.language;
  const isArabic = lang === 'ar';

  const ButtonStyle = "bg-transparent border-2 font-semibold font-jost transition-colors border-teritary relative flex items-center justify-center w-full px-4 py-4 rounded-md text-accent";
  const SelectedButtonStyle = twMerge(ButtonStyle, " bg-primary text-foreground border-transparent");


  return (
    <>
      <MaxWidthContainer id={"services"} section={true} className="py-12">
        <div className="flex flex-col justify-center items-center max-w-[600px] text-center mx-auto">
          <TitleCard className="mb-4">{t('services_card')}</TitleCard>
          <h2 className="mb-12 text-accent font-jost text-5xl font-bold">{t('services_heading')}</h2>
        </div>

        {/* Content Container */}
        <div className="grid gap-5 max-w-[600px] lg:max-w-full mx-auto grid-cols-1 lg:grid-cols-[1fr_2fr]">

          {/* Buttons */}
          <div className="flex gap-5 flex-col items-center mb-auto justify-center">
            <button onClick={() => { setSelected('web') }} className={selected === 'web' ? SelectedButtonStyle : ButtonStyle}>
              <Global size="32" className={twMerge("absolute", isArabic ? "right-4" : "left-4")} />
              <span className="text-lg">{t('services_web_button')}</span>
            </button>
            <button onClick={() => { setSelected('mobile') }} className={selected === 'mobile' ? SelectedButtonStyle : ButtonStyle}>
              <Mobile size="32" className={twMerge("absolute", isArabic ? "right-4" : "left-4")} />
              <span className="text-lg">{t('services_mobile_button')}</span>
            </button>
          </div>

          {/* Content */}
          <div className="flex lg:max-h-[400px] flex-col lg:flex-row gap-4 pb-12">
            <img aria-hidden alt="web" className="object-cover rounded-md max-w-[400px]" src={`assets/${selected === 'web' ? "service-1.jpg" : "service-2.jpg"}`} />
            <div className="max-w-[400px] flex flex-col justify-between">
              <h3 className="text-3xl font-jost font-semibold text-accent mb-6">
                {
                  selected === 'web'
                    ? t('services_web_heading')
                    : t('services_mobile_heading')
                }
              </h3>
              <p className="mb-6 text-muted">
                {
                  selected === 'web'
                    ? t('services_web_description')
                    : t('services_mobile_description')
                }
              </p>
              <ul className="mb-4 space-y-2">
                <li className="flex items-center gap-2"><ArrowCircleRight size="24" className="text-primary" /><span>
                  {
                    selected === 'web'
                      ? t('services_web_feature_1')
                      : t('services_mobile_feature_1')
                  }
                </span></li>
                <li className="flex items-center gap-2"><ArrowCircleRight size="24" className="text-primary" /><span>
                  {
                    selected === 'web'
                      ? t('services_web_feature_2')
                      : t('services_mobile_feature_2')
                  }
                </span></li>
                <li className="flex items-center gap-2"><ArrowCircleRight size="24" className="text-primary" /><span>
                  {
                    selected === 'web'
                      ? t('services_web_feature_3')
                      : t('services_mobile_feature_3')
                  }
                </span></li>
              </ul>
              <Button>{t('services_cta')}</Button>
            </div>
          </div>

        </div>

      </MaxWidthContainer>
      <div aria-labelledby='achievements' className="w-full relative py-12 bg-decoration">
        <h2 id="achievements" className="hidden">Achievements</h2>
        <MaxWidthContainer width={'md'} section={false} className="py-12 px-14 justify-between flex flex-col md:flex-row gap-10">

          {
            supposedAPIData.map(item => <AnimatedDiv delay={item.delay} key={item.id} className="flex flex-col text-foreground text-center justify-center items-center gap-2">
              {item.icon}

              <h3 className="flex flex-col gap-2">
                <span className="text-[60px] leading-[70px] font-bold font-jost">
                  {item.number}
                </span>
                <span className="text-[20px]">
                  {item.title[lang]}
                </span>
              </h3>

              <hr aria-hidden className="border-0 w-[80px] mt-2 h-[1px] bg-[#ffffff2f]" />
            </AnimatedDiv>)
          }

        </MaxWidthContainer>
      </div>
    </>
  )
}

const supposedAPIData = [
  {
    id: "0",
    icon: <EmojiHappy className="w-full" size="60" />,
    number: 75,
    title: {
      en: "Happy Clients",
      ar: " "
    },
    delay: 0.25,
  },
  {
    id: "1",
    icon: <Global className="w-full" size="60" />,
    number: 103,
    title: {
      en: "Projects Completed",
      ar: " "
    },
    delay: 0.45,
  },
  {
    id: "2",
    icon: <People className="w-full" size="60" />,
    number: 25,
    title: {
      en: "Dedicated Staff",
      ar: " "
    },
    delay: 0.65,
  },
  {
    id: "3",
    icon: <Award className="w-full" size="60" />,
    number: 3,
    title: {
      en: "Awards Achieved",
      ar: " "
    },
    delay: 0.85,
  }
];


export default Services