import React from 'react'
import TitleCard from '../../../ui/titleCard'
import { twMerge } from 'tailwind-merge';
import Button from '../../../ui/button';
import { useTranslation } from 'react-i18next';
import AnimatedDiv from '../../../ui/animatedDiv';

const ContactUs = () => {

  const { t } = useTranslation();

  const InputStyle = "px-3 py-4 border border-teritary rounded-md placeholder:text-muted";

  return (
    <section id="contact-us" className="w-full relative pt-12 px-5 my-12">
      <div aria-hidden className="bg-decoration z-[-1] absolute h-[300px] w-full top-0 left-0 "></div>

      <AnimatedDiv>
        <form className="bg-background max-w-[745px] border border-teritary w-full p-8 px-12 md:mx-auto  mt-12 rounded-md">
          <div className="flex flex-col gap-6 items-center justify-center text-center">
            <TitleCard>{t("contact_card")}</TitleCard>
            <h2 className="mb-6 max-w-[600px] text-accent font-jost text-5xl font-bold">{t("contact_heading")}</h2>
          </div>
          <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
            <input placeholder={t("contact_input_name")} className={InputStyle} />
            <input placeholder={t("contact_input_email")} className={InputStyle} />
            <input placeholder={t("contact_input_mobile")} className={InputStyle} />
            <input placeholder={t("contact_input_subject")} className={InputStyle} />
            <textarea placeholder={t("contact_input_message")} className={twMerge(InputStyle, "md:col-span-2")}></textarea>
          </div>
          <Button fit={false} className="mt-4">{t("contact_submit_button")}</Button>
        </form>
      </AnimatedDiv>

    </section>
  )
}

export default ContactUs
