import React, { useEffect } from 'react'
import { useTranslation } from 'react-i18next'
import { twMerge } from 'tailwind-merge';

const LanguageSwitcher = ({ className, blur, ...rest }) => {

    const { i18n } = useTranslation();
    const isArabic = i18n.language === 'ar'

    useEffect(() => {
        document.getElementsByTagName('html')[0].setAttribute('dir', i18n.language === 'ar' ? 'rtl' : 'ltr');
    }, [i18n.language])

    return <a href="./" {...rest} onBlur={blur} className={twMerge("flex items-center gap-2 font-semibold", className)} onClick={() => {i18n.changeLanguage(isArabic ? 'en' : 'ar')}}>
        {
            isArabic
                ? <>
                    <span>English</span><img alt="US Flag" aria-hidden src="https://flagsapi.com/US/shiny/24.png"></img>
                </>
                : <>
                    <span></span><img alt="UAE Flag" aria-hidden src="https://flagsapi.com/AE/shiny/24.png"></img>
                </>
        }
    </a>
}

export default LanguageSwitcher