import React, { useEffect } from 'react'
import TitleCard from '../../../ui/titleCard'
import MaxWidthContainer from '../../../ui/maxWidthContainer';
import { twMerge } from 'tailwind-merge';
import { useTranslation } from 'react-i18next';

import AnimatedDiv from '../../../ui/animatedDiv';

import { ArrowRight2, ArrowLeft2 } from 'iconsax-react';

import { useSwiper } from 'swiper/react';
import { Swiper, SwiperSlide } from 'swiper/react';
import { Pagination } from 'swiper/modules';

const SlideNextButton = () => {
  const swiper = useSwiper();

  useEffect(() => {
    const intervalFunction = () => swiper.slideNext();
    const Interval = setInterval(intervalFunction, 3000);

    return () => clearInterval(Interval);
  }, [])

  return (
    <button onClick={() => swiper.slideNext()}><ArrowRight2 size="32" color="#FF8A65" /></button>
  );
}

const SlidePrevButton = () => {
  const swiper = useSwiper();

  return (
    <button onClick={() => swiper.slidePrev()}><ArrowLeft2 size="32" color="#FF8A65" /></button>
  );
}

const Client = ({ imgSrc, name, isArabic }) => {

  return <AnimatedDiv className="relative group h-[300px] w-[300px]">
    <div className={twMerge("relative h-[250px] w-[260px] rounded-md shadow-md bg-teritary p-2")}>
      <img aria-hidden alt="company-name" src={`assets/${imgSrc}`} className="w-full h-full object-contain !rounded-md pointer-events-none" />
    </div>
    <div className={twMerge("flex border border-teritary px-4 group-hover:bg-primary transition-colors pb-3 h-[260px] rounded-md w-[260px] bottom-0 absolute z-[-1]", isArabic ? "left-0" : "right-0")}>
      <span className="mt-auto text-accent transition-colors group-hover:text-foreground">{name}</span>
    </div>
  </AnimatedDiv>
}

const Clients = () => {

  const { t, i18n } = useTranslation();
  const isArabic = i18n.language === 'ar';

  return (
    <MaxWidthContainer id="clients" section={true} className="py-12">

      <AnimatedDiv className="flex flex-col mb-12 justify-center items-center text-center mx-auto">
        <TitleCard className="mb-4">{t('clients_card')}</TitleCard>
        <h2 className="mb-6 max-w-[600px] leading-[60px] text-accent font-jost text-5xl font-bold">{t('clients_heading')}</h2>
      </AnimatedDiv>

      <div className="flex justify-center items-center mx-auto max-w-[1270px]">
        <Swiper
          breakpoints={{
            640: {
              slidesPerView: 2,
              spaceBetween: 20,
            },
            1024: {
              slidesPerView: 3,
              spaceBetween: 30,
            },
          }}
          freeMode={true}
          loop={true}
          modules={[Pagination]}
          className="mySwiper"
        >
          {
            supposedAPIData.map(item => <SwiperSlide className="flex justify-center items-center" key={item.id}>
              <Client {...item} isArabic={isArabic} />
            </SwiperSlide>)
          }
          <div className="mt-6 flex gap-4 mb-2 w-fit mx-auto">
            <SlidePrevButton />
            <SlideNextButton />
          </div>
        </Swiper>
      </div>

    </MaxWidthContainer>
  )
}

const supposedAPIData = [
  {
    id: "1",
    name: "Client B",
    imgSrc: "client-1.jpg"
  },
  {
    id: "2",
    name: "Client C",
    imgSrc: "client-2.jpg"
  },
  {
    id: "3",
    name: "Client D",
    imgSrc: "client-3.jpg"
  },
  {
    id: "4",
    name: "Client E",
    imgSrc: "client-4.jpg"
  },
  {
    id: "5",
    name: "Client F",
    imgSrc: "client-5.jpg"
  },
  {
    id: "6",
    name: "Client G",
    imgSrc: "client-6.jpg"
  },
  {
    id: "7",
    name: "Client H",
    imgSrc: "client-7.jpg"
  },
  {
    id: "8",
    name: "Client I",
    imgSrc: "client-8.jpg"
  },
  {
    id: "9",
    name: "Client J",
    imgSrc: "client-9.jpg"
  },
  {
    id: "10",
    name: "Client K",
    imgSrc: "client-10.jpg"
  },
]

export default Clients
