import React, { useState } from 'react'
import { Link } from 'react-router-dom'
import LanguageSwitcher from '../ui/langaugeSwitcher'
import { useTranslation } from 'react-i18next'
import { HambergerMenu } from 'iconsax-react';
import { twMerge } from 'tailwind-merge';

const DesktopNavigation = ({ lang }) => {

  return <nav className="hidden lg:flex">
    < ul className="flex gap-7" >

      {
        supposedAPIData.map(item => <li>
          <Link key={item.id} className="text-[#777] font-[500] tracking-wide hover:text-secondary transition-colors" href={item.href}>{item.name[lang]}</Link>
        </li>)
      }

    </ul >
  </nav >
}

const MobileNavigation = ({ isOpen, lang, toggleMobileNav }) => {

  return <nav className={twMerge("overflow-hidden border-t border-t-transparent lg:hidden fixed top-0 translate-y-[89px] h-[1000px] max-h-[0px] w-full left-0 transition-all duration-[600ms]", isOpen && " border-t-[#77777757] max-h-[400px]")}>
    <ul className="flex flex-col items-start shadow-md pt-8 px-5 gap-5 h-fit bg-white">

      {
        supposedAPIData.map(item => <li key={item.id} className="text-[#777] font-[500] tracking-wide">
          <Link tabIndex={isOpen ? 0 : -1} to={item.href}>{item.name[lang]}</Link>
        </li>)
      }

      <LanguageSwitcher blur={toggleMobileNav} tabIndex={isOpen ? 0 : -1} className="p-5 w-full border-t flex items-center justify-center" />
    </ul>
  </nav>

}

const Header = () => {
  const [MobileNavOpened, setMobileNav] = useState(false);
  const { i18n } = useTranslation();
  const lang = i18n.language;

  const toggleMobileNav = () => {
    setMobileNav(prev => !prev);
  }

  return (
    <header className="fixed bg-white shadow-sm z-40 w-full top-0 left-0 flex justify-between items-center h-[90px] px-5 md:px-12">

      <Link to={"/"}>
        <img alt="microdata-logo" src="assets/logo.png" className="h-full w-[150px]" />
      </Link>

      <div className="flex gap-8 items-center">

        <DesktopNavigation lang={lang} />
        <button onClick={() => { toggleMobileNav() }} className="lg:hidden p-1 border border-muted/20 rounded-md focus:border-primary">
          <span aria-hidden className="hidden">Hamburger</span>
          <HambergerMenu size="32" />
        </button>
        <MobileNavigation lang={lang} toggleMobileNav={toggleMobileNav} isOpen={MobileNavOpened} />
        <div aria-hidden className="hidden md:block h-[30px] bg-[#5555557a] border-0 my-auto w-[1px]"></div>
        <div className="hidden md:block">
          <LanguageSwitcher />
        </div>
      </div>

    </header>
  )
}

const supposedAPIData = [
  {
    id: "0",
    name: {
      en: "Home",
      ar: " "
    },
    href: "#"
  },
  {
    id: "1",
    name: {
      en: "About",
      ar: ""
    },
    href: "#"
  },
  {
    id: "2",
    name: {
      en: "Services",
      ar: ""
    },
    href: "#"
  },
  {
    id: "3",
    name: {
      en: "Portfolio",
      ar: ""
    },
    href: "#"
  },
  {
    id: "4",
    name: {
      en: "Contact Us",
      ar: " "
    },
    href: "#"
  },
]

export default Header