import TitleCard from '../../../ui/titleCard';
import Button from '../../../ui/button';
import { twMerge } from 'tailwind-merge';

// Import Swiper React components
import { Swiper, SwiperSlide } from 'swiper/react';

// import required modules
import { EffectFade, Navigation, Pagination } from 'swiper/modules';
import { useTranslation } from 'react-i18next';

import { useSwiper } from 'swiper/react';
import { useEffect } from 'react';

const NextButton = () => {

  const swiper = useSwiper();

  useEffect(() => {
    const intervalFunction = () => swiper.slideNext();
    const Interval = setInterval(intervalFunction, 3000);

    return () => clearInterval(Interval);
  }, [])

  return <hr aria-hidden className="hidden" />
}

const Hero = () => {

  const { t, i18n } = useTranslation();
  const lang = i18n.language;
  const isArabic = lang === 'ar';

  const CarouselImageStyle = "!absolute pointer-events-none h-full w-full object-cover object-right object-bottom";

  return (
    <section id="hero" className="w-full mb-12 lg:h-dvh h-[500px] pt-[90px] overflow-hidden">
      <div className="max-h-[400px] lg:max-h-full h-full my-auto">
        <Swiper
          spaceBetween={30}
          effect={'fade'}
          loop={true}
          navigation={true}
          modules={[EffectFade, Navigation, Pagination]}
          className="mySwiper h-full"
        >
          <NextButton />
          {
            supposedAPIData.map((item) => <SwiperSlide key={item.id} className="flex justify-center bg-white items-center">

              <div className={twMerge("w-full shadow-md flex flex-col gap-5 lg:items-end bg-white/40 py-8 px-8 absolute sm:px-14 lg:px-5 z-20", isArabic ? "right-0 max-w-[950px] lg:rounded-l-[300px]" : "lg:rounded-r-[300px] left-0 max-w-[1100px]")}>
                <div className="w-fit space-y-[30px] lg:px-5">

                  <TitleCard bordered={false}>{item.cardTitle[lang]}</TitleCard>
                  <h1 className={twMerge("text-3xl md:text-3xl lg:text-7xl max-w-[400px] lg:max-w-[800px] text-accent font-bold xl:leading-[84px]", isArabic && "lg:max-w-[620px]")}>{item.title[lang] ? item.title[lang] : item.title.en}</h1>

                  <Button>
                    {t('hero_cta')}
                  </Button>

                </div>

              </div>

              {
                item.isVideo
                  ? <video
                    id="background-video"
                    aria-hidden
                    loop
                    className={twMerge("h-full w-full object-cover object-right", isArabic && "scale-x-[-100%]")}
                    src={`assets/${item.imgSrc}`}
                    muted
                    autoPlay={true}>
                  </video>

                  : <img alt={item.imgSrc} className={twMerge(CarouselImageStyle, isArabic && "scale-x-[-100%]")} aria-hidden src={`assets/${item.imgSrc}`} />
              }
            </SwiperSlide>)
          }
        </Swiper>
      </div>
    </section>
  )
}

const supposedAPIData = [
  {
    id: "0",
    cardTitle: {
      en: "Welcome to MicroData",
      ar: "    ",
    },
    title: {
      en: "Empowering Your Business, Enhancing Your Processes",
      ar: "   ",
    },
    imgSrc: "video-1.mp4",
    isVideo: true,
  },
  {
    id: "1",
    cardTitle: {
      en: "Welcome to MicroData",
      ar: "    ",
    },
    title: {
      en: "Integrating Business, Technology, and Innovation",
      ar: "   ",
    },
    imgSrc: "carousel-5.jpg",
    isVideo: false,
  },
  {
    id: "2",
    cardTitle: {
      en: "Welcome to MicroData",
      ar: "    ",
    },
    title: {
      en: "Empowering Your Business, Enhancing Your Processes",
      ar: "   ",
    },
    imgSrc: "carousel-3.jpg",
    isVideo: false,
  },
];


export default Hero