import { twMerge } from "tailwind-merge";

const MaxWidthContainer = ({ width = null, section = true, children, className, id, ...rest }) => {

    const getWidth = () => {
        switch (width) {
            case "sm": return "max-w-[px]";
            case "md": return "max-w-[1296px]";
            case "lg": return "max-w-[1320px]";
            default: return "max-w-[1296px]"
        }
    }

    const maxWidth = getWidth();

    return section ? <section id={id} className={twMerge(`w-full px-[12px] mx-auto`, maxWidth, className)}>
        {children}
    </section>
        : <div className={twMerge(`${getWidth} w-full px-[12px] mx-auto`, maxWidth, className)}>
            {children}
        </div>

}

export default MaxWidthContainer
