import React from 'react'
import { twMerge } from 'tailwind-merge';

const TitleCard = ({ children, bordered = true, className, ...rest }) => {
    return (
        <p className={twMerge("py-1 px-4 bg-background text-secondary w-fit rounded-md font-[500]", bordered && "border border-teritary", className)} {...rest}>
            {children}
        </p>
    )
}

export default TitleCard