import { motion } from "framer-motion"

const AnimatedDiv = ({ children, delay, duration, className }) => {
    return (
        <motion.div
            className={className}
            viewport={{ once: true }}
            variants={{
                hidden: {
                    y: 100,
                    opacity: 0
                },
                visible: {
                    y: 0,
                    opacity: 1
                }
            }} initial="hidden"
            transition={{ duration: duration || 0.5, delay: delay || 0.25 }}
            whileInView="visible" >

            {children}

        </motion.div>
    )
}

export default AnimatedDiv
