import React, { useEffect } from 'react'
import TitleCard from '../../../ui/titleCard'
import MaxWidthContainer from '../../../ui/maxWidthContainer';
import { twMerge } from 'tailwind-merge';
import { Link } from 'react-router-dom'
import { useTranslation } from 'react-i18next';

import { QuoteDown } from 'iconsax-react';

import { useSwiper } from 'swiper/react';
import { Swiper, SwiperSlide } from 'swiper/react';
import { Pagination } from 'swiper/modules';
import AnimatedDiv from '../../../ui/animatedDiv';

const Testimony = ({ name, role, imgSrc, comment, lang }) => {
  return <article className="flex flex-col pt-9 justify-center items-center">

    {/* Comment Container */}
    <div className={twMerge("mb-12 min-h-[170px] p-6 w-[411px] relative rounded-md below-triangle pt-12 text-center text-muted border border-teritary", lang === 'ar' ? "below-triangle-ar" : "below-triangle")}>
      <div className="absolute bg-background border border-teritary w-[60px] h-[60px] rounded-full top-0 left-[50%] translate-x-[-50%] translate-y-[-50%]">
        <QuoteDown variant="Bold" size="32" className="text-primary absolute right-[50%] bottom-[50%] translate-y-[50%] translate-x-[50%]" />
      </div>
      <p>{comment[lang]}</p>
    </div>

    <div className="w-[100px] h-[100px] mb-4 rounded-[100px] mx-auto overflow-hidden">
      <img alt={`${name.en}'s photo`} className="w-full h-full" src={`./assets/${imgSrc}`} />
    </div>

    <span className="font-jost font-semibold text-accent text-2xl mb-1">{name[lang]}</span>
    <span className="text-muted">{role}</span>

  </article>
}

const Testimonies = () => {

  const { t, i18n } = useTranslation();
  const lang = i18n.language;
  const isArabic = lang === 'ar';

  return (
    <MaxWidthContainer id="testimonials" section={true} className="py-12">

      <AnimatedDiv className="flex flex-col mb-6 justify-center items-center text-center mx-auto">
        <TitleCard className="mb-4">{t('testimonials_card')}</TitleCard>
        <h2 className="mb-6 max-w-[600px] leading-[60px] text-accent font-jost text-5xl font-bold">{t('testimonials_heading')}</h2>
      </AnimatedDiv>

      <div className="flex justify-center items-center mx-auto max-w-[1270px]">
        <Swiper
          breakpoints={{

            1094: {
              slidesPerView: 3,
              spaceBetween: 30,
            },
          }}
          freeMode={true}
          loop={true}
          modules={[Pagination]}
          className="mySwiper"
        >
          {
            supposedAPIData.map(item => <SwiperSlide key={item.id}><Testimony {...item} lang={lang} /></SwiperSlide>)
          }

        </Swiper>
      </div>

    </MaxWidthContainer>
  )
}

