const Footer = () => {

  const HeaderStyle = "text-2xl font-jost font-semibold mb-6";
  const LinkStyle = "flex items-center gap-2";
  const ContactLinkStyle = "h-[30px] w-[30px] rounded-md hover:scale-[1.1] cursor-pointer flex items-center justify-center bg-primary";

  const { i18n } = useTranslation();
  const lang = i18n.language;
  const isArabic = lang === 'ar';

  return (
    <footer className="mt-12">
      <div className="bg-decoration py-12 px-3">

        <div className="max-w-[1300px] justify-center items-center sm:items-start sm:justify-start gap-14 md:gap-0 flex flex-wrap my-12 mx-auto ">

          <div className="w-[325px] text-foreground">
            <h2 className={HeaderStyle}>Our Office</h2>

            <ul className="space-y-1">
              <li className={LinkStyle}>
                <Location size="20" className="text-white" variant="Bold" /><span className="hover:tracking-wider transition-all">Maadi, Cairo, Egypt</span>
              </li>
              <li className={LinkStyle}>
                <Call size="20" className="text-white" variant="Bold" /><span className="hover:tracking-wider transition-all">+012 345 67890</span>
              </li>
              <li className={LinkStyle}>
                <Sms size="20" className="text-white" variant="Bold" /><span className="hover:tracking-wider transition-all">info@microdata-sw.com</span>
              </li>
            </ul>
            <ul className="flex mt-3 items-center gap-4">
              <li className={ContactLinkStyle}><LinkedInSVG /></li>
              <li className={ContactLinkStyle}><YouTubeSVG /></li>
              <li className={ContactLinkStyle}><FacebookSVG /></li>
              <li className={ContactLinkStyle}><TwitterSVG /></li>
            </ul>
          </div>



          {
            supposedAPIData.map(item => <div className="w-[325px] text-foreground">
              <h2 className={HeaderStyle}>{item.name[lang]}</h2>

              <ul className="space-y-1">
                {
                  item.Links.map(subLink => <li><Link to={subLink.href} className={LinkStyle}>
                    <ArrowRight2 variant="Outline" size="20" className={isArabic && "scale-x-[-100%]"} /><span className="hover:tracking-wider transition-all">{subLink.name[lang]}</span>
                  </Link></li>)
                }
              </ul>

            </div>)
          }

        </div>

      </div>


      <div className="bg-[#000B1C] py-6 px-3 text-center">
        <span className="text-foreground"> MicroData, All Right Reserved.</span>
      </div>

    </footer>
  )
}

