import React from 'react'
import TitleCard from '../../../ui/titleCard'
import MaxWidthContainer from '../../../ui/maxWidthContainer';
import { useTranslation } from 'react-i18next';
import AnimatedDiv from '../../../ui/animatedDiv';

const LinkedInSVG = () => <svg width="20" height="18" viewBox="0 0 20 18" fill="none" xmlns="http://www.w3.org/2000/svg">
  <path d="M4 2.001C3.99974 2.53143 3.78877 3.04004 3.41351 3.41492C3.03825 3.78981 2.52943 4.00027 1.999 4C1.46857 3.99974 0.959965 3.78877 0.585079 3.41351C0.210194 3.03825 -0.000264966 2.52943 2.50361e-07 1.999C0.000265467 1.46857 0.211233 0.959965 0.586494 0.585079C0.961754 0.210194 1.47057 -0.000264966 2.001 2.50361e-07C2.53143 0.000265467 3.04004 0.211233 3.41492 0.586494C3.78981 0.961754 4.00027 1.47057 4 2.001ZM4.06 5.481H0.0600002V18.001H4.06V5.481ZM10.38 5.481H6.4V18.001H10.34V11.431C10.34 7.771 15.11 7.431 15.11 11.431V18.001H19.06V10.071C19.06 3.901 12 4.131 10.34 7.161L10.38 5.481Z" fill="#ffffff" />
</svg>



const TeamMember = ({ name, imgSrc, delay }) => {
  return <AnimatedDiv delay={delay} tabIndex={0} className="group  mx-auto relative flex items-center h-[330px] w-[350px] rounded-md justify-center">

    <div className="flex group-hover:bg-primary transition-colors flex-col justify-between border border-teritary h-full w-[80%] rounded-md py-4 ml-auto items-center">
      <span className="font-jost text-lg text-accent group-hover:text-foreground transition-colors font-[500]">{name}</span>
      <div className="bg-primary p-2 rounded-md shadow-sm">
        <LinkedInSVG />
      </div>
    </div>

    <div className="bg-red-500 h-[55%] w-[200px] absolute shadow-md left-4 rounded-md">
      <img alt={`${name}'s profile`} className="w-full h-full object-cover object-top rounded-md" src={`assets/${imgSrc}`} />
    </div>

  </AnimatedDiv>
}

const Team = () => {

  const { t } = useTranslation();

  return (
    <MaxWidthContainer id="team" section={true} className="py-12">

      <div className="flex flex-col mb-12 justify-center items-center text-center mx-auto">
        <TitleCard className="mb-4">{t('team_card')}</TitleCard>
        <h2 className="mb-6 max-w-[600px] leading-[60px] text-accent font-jost text-5xl font-bold">{t('team_heading')}</h2>
      </div>

      <div className="grid grid-cols-1 lg:grid-cols-3 gap-5">
        {
          supposedAPIData.map(item => <TeamMember key={item.id} {...item} />)
        }
      </div>

    </MaxWidthContainer>
  )
}

